
.app {
  margin: 0;
}

.todo-list {
  margin: 0 auto;
  background:  rgb(73, 71, 71);
  border-radius: 4px;
  padding: 8px;
  max-width: 480px;
}

.form {
  height: 100px;
  background-color: white;
  display: flex;
  border-radius: 4px;
}

.inputForm {
  margin: auto;
  width: 70%;
  display: flex;
  justify-content: space-between;
}

.input {
  height: 40px;
  width: 280px;
  border-radius: 4px;
  box-sizing: border-box;
  outline: none;
  padding: 8px;
  font-size: 24px;
}

.addButton {
  background-color: rgb(73, 71, 71);
  border-radius: 4px;
  width: 40px;
  height: 40px;
  border: none;
  color: white;
  font-size: 32px;
  font-weight: bold;
  text-align: center;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
}

.addButton:hover {
  color: yellow;
  background-color: rgb(123, 123, 126);
}

.todo {
  background: whitesmoke;
  padding: 8px;
  font-size: 24px;
  color:rgb(53, 53, 52);
  margin-top: 8px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.removeButton {
  background-color: rgb(64, 64, 68);
  border-radius: 4px;
  width: 30px;
  height: 30px;
  border: none;
  font-size: 24px;
  color: white;
  font-weight: bold;
  text-align: center;
  box-sizing: border-box;
  padding: 0;
  cursor: pointer;
}

.removeButton:hover {
  background-color: rgb(123, 123, 126);
  color: red;
}